import React from "react";
import {
  Loading,
  Box,
  FirstLine,
  SecondLine,
  ThirdLine,
  FourthLine,
} from "./styled";
import { AnimatePresence, motion } from "framer-motion";

const box = {
  hidden: {
    opacity: 0,
  },
  visible: {
    opacity: 1,
    transition: { duration: 1, ease: [0.83, 0, 0.17, 1] },
  },
  exit: {
    opacity: 0,
    transition: { duration: 1 },
  },
};

const First = {
  hidden: {
    pathLength: 0,
    pathOffset: 1,
  },
  visible: {
    pathLength: 1,
    pathOffset: 0,
    transition: {
      delay: 1,
      duration: 1.5,
      ease: [0.83, 0, 0.17, 1],
    },
  },
};

const Second = {
  hidden: {
    pathLength: 0,
    pathOffset: 1,
  },
  visible: {
    pathLength: 1,
    pathOffset: 2,
    transition: {
      delay: 2,
      duration: 1,
      ease: [0.83, 0, 0.17, 1],
    },
  },
};

const Third = {
  hidden: {
    pathLength: 0,
    pathOffset: 1,
  },
  visible: {
    pathLength: 1,
    pathOffset: 0,
    transition: {
      delay: 1,
      duration: 1.5,
      ease: [0.83, 0, 0.17, 1],
    },
  },
};

const Fourth = {
  hidden: {
    pathLength: 0,
    pathOffset: 1,
  },
  visible: {
    pathLength: 1,
    pathOffset: 2,
    transition: {
      delay: 2,
      duration: 1,
      ease: [0.83, 0, 0.17, 1],
    },
  },
};

export default function LoadingPage() {
  return (
    <Loading>
      <Box>
        <AnimatePresence>
          <motion.svg
            layout
            key="1"
            width="150"
            height="80"
            viewBox="0 0 150 90"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
            variants={box}
            initial="hidden"
            animate="visible"
            exit="hidden"
          >
            <motion.rect
              width="120"
              height="80"
              x="1"
              rx="5"
              ry="5"
              fill="#C4C4C4"
              variants={box}
              initial="hidden"
              animate="visible"
              exit="hidden"
            />
          </motion.svg>
        </AnimatePresence>
      </Box>

      <FirstLine>
        <AnimatePresence>
          <motion.svg
            key="2"
            width="100"
            height="80"
            viewBox="0 0 100 90"
            fill="transparent"
            xmlns="http://www.w3.org/2000/svg"
          >
            <g stroke="transparent" strokeWidth="7">
              <motion.path
                d="M1 0V80"
                stroke="#C24D2C"
                variants={First}
                initial="hidden"
                animate="visible"
              />
            </g>
          </motion.svg>
        </AnimatePresence>
      </FirstLine>

      <SecondLine>
        <AnimatePresence>
          <motion.svg
            key="2"
            width="100"
            height="80"
            viewBox="0 0 100 80"
            fill="transparent"
            xmlns="http://www.w3.org/2000/svg"
          >
            <g stroke="transparent" strokeWidth="10">
              <motion.path
                d="M1 0V80"
                stroke="#1A2639"
                variants={Second}
                initial="hidden"
                animate="visible"
              />
            </g>
          </motion.svg>
        </AnimatePresence>
      </SecondLine>

      <ThirdLine>
        <AnimatePresence>
          <motion.svg
            key="3"
            width="100"
            height="200"
            viewBox="-10 10 100 200"
            fill="transparent"
            xmlns="http://www.w3.org/2000/svg"
          >
            <g stroke="transparent" strokeWidth="6">
              <motion.path
                d="M85.8528 1L0.999969 85.8528"
                stroke="#1A2639"
                variants={Third}
                initial="hidden"
                animate="visible"
              />
            </g>
          </motion.svg>
        </AnimatePresence>
      </ThirdLine>

      <FourthLine>
        <AnimatePresence>
          <motion.svg
            key="4"
            width="100"
            height="200"
            viewBox="-2 -2 100 200"
            fill="transparent"
            xmlns="http://www.w3.org/2000/svg"
          >
            <g stroke="transparent" strokeWidth="6">
              <motion.path
                d="M43.4264 43.4264L0.999985 0.999985"
                stroke="#1A2639"
                variants={Fourth}
                initial="hidden"
                animate="visible"
              />
            </g>
          </motion.svg>
        </AnimatePresence>
      </FourthLine>
    </Loading>
  );
}
